@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;

400;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
  text-decoration: none;
  list-style: none;
  outline: none;
}

html {
  scroll-behavior: smooth;
}

::selection {
  background-color: rgb(0, 126, 176);
  color: rgb(255, 255, 255);
}

body {
  background-color: rgb(26, 26, 26);
  color: rgb(255, 255, 255);
  transition: 0.1s ease;
}

body.light {
  background-color: rgb(248, 248, 248);
}

body::-webkit-scrollbar {
  width: 13px;
  background-color: rgb(126, 126, 126);
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(41, 41, 41);
}

body::-webkit-scrollbar-thumb:hover {
  background-color: rgb(58, 58, 58);
}

.small__title {
  font-size: 18px;
  font-weight: 400;
  opacity: 0.5;
  text-align: center;
}

.title {
  margin-top: 6px;
  font-size: 32px;
  font-weight: 800;
  text-transform: uppercase;
  text-align: center;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

/* ========================= Light Mode =========================== */

.light .small__title {
  opacity: 0.7;
  color: rgb(26, 26, 26);
}

.light .title {
  color: rgb(26, 26, 26);
}

.light .header__section.topNav-active {
  background-color: rgb(248, 248, 248);
}

.light .header__navbar .logo .logo_icon {
  fill: rgb(26, 26, 26);
}

.light .header__navbar .nav-links ul li a {
  color: rgb(26, 26, 26);
  font-weight: 600;
}

.light .header__navbar .nav-links ul li a::after {
  background-color: rgb(131, 131, 131);
}

.light .header__navbar .nav-links ul li a:hover::after {
  width: 100%;
  background-color: rgb(26, 26, 26);
}

.light .header__navbar .color_theme_icon .icon_btn {
  color: rgb(26, 26, 26);
}

.light .header__navbar .navside {
  background-color: rgb(6, 27, 36);
}

.light .header__navbar .burger .bar {
  background-color: rgb(26, 26, 26);
}

.light .content__blocks .content__text_block .content__title {
  color: rgb(26, 26, 26);
}

.light .content__blocks .content__text_block p {
  color: rgb(26, 26, 26);
}

.light .content__blocks .content__text_block .watching a {
  color: rgb(26, 26, 26);
}

.light .content__blocks .content__text_block .watching a i {
  color: rgb(255, 255, 255);
}

.light .content__blocks .content__text_block .watching a span:hover {
  border-bottom: 1px solid rgb(26, 26, 26);
}

.light .skills__blocks .skills_block {
  background-color: rgb(0, 31, 43);
}

.light .skills__blocks .skills_block:hover {
  box-shadow: 0 5px 10px 5px rgb(0, 31, 43);
}

.light .about__items .about__item_text p {
  color: rgb(26, 26, 26);
}

.light .contact__items .contact__inputs_item input {
  background: none;
  border: 1px solid rgb(26, 26, 26);
  color: rgb(26, 26, 26);
}

.light .contact__items .contact__inputs_item textarea {
  background: none;
  border: 1px solid rgb(26, 26, 26);
  color: rgb(26, 26, 26);
}

.light .footer__section {
  border-top: 1px solid rgb(26, 26, 26);
}

.light .footer__logo_item .footer__logo_svg .footer__logo_icon {
  fill: rgb(26, 26, 26);
}

.light .footer__logo_item .footer__text {
  color: rgb(26, 26, 26);
}

.light .footer_item .social__title {
  color: rgb(26, 26, 26);
  font-weight: 700;
}

.light .footer_item .social__links ul li a {
  color: rgb(26, 26, 26);
}

.light .copyright {
  border-top: 1px solid rgb(26, 26, 26);
}

.light .copyright p {
  color: rgb(26, 26, 26);
}

.light .copyright p:nth-child(2) a {
  color: rgb(26, 26, 26);
}

/* ========================= Light Mode END =========================== */

.header__section {
  position: fixed;
  top: 0;
  left: 50%;
  width: 101%;
  height: 100px;
  transform: translateX(-50%);
  transition: 0.2s ease;
  z-index: 9999;
}

.header__section.topNav-active {
  background-color: rgb(26, 26, 26);
  box-shadow: 0px 0px 7px rgb(43, 43, 43);
  height: 80px;
}

.header__section.topNav-active .header__navbar {
  height: 80px;
}

.header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  transition: 0.2s ease;
}

.header__navbar .logo {
  max-width: 272px;
  min-width: 80px;
}

.header__navbar .logo .logo_icon {
  width: 100%;
  fill: rgb(255, 255, 255);
}

.header__navbar .nav-links ul {
  display: flex;
}

.header__navbar .nav-links ul li a {
  position: relative;
  color: rgb(255, 255, 255);
  padding: 15px 25px;
  letter-spacing: 1px;
}

.header__navbar .nav-links ul li a::after {
  position: absolute;
  content: "";
  top: 100%;
  left: 0;
  width: 0;
  height: 3px;
  background-color: rgb(131, 131, 131);
  transition: 0.2s ease;
}

.header__navbar .nav-links ul li a:hover::after {
  width: 100%;
  background-color: rgb(255, 255, 255);
}

.header__navbar .nav-links ul li a.active::after {
  width: 100%;
}

.header__navbar .color_theme_icon .icon_btn {
  font-size: 33px;
  background: none;
  padding: 5px 8px;
  color: rgb(255, 255, 255);
  border: none;
  cursor: pointer;
  transition: 0.2s ease;
}

.header__navbar .burger {
  cursor: pointer;
  padding: 5px 10px;
  display: none;
}

.header__navbar .burger .bar {
  width: 30px;
  height: 3px;
  background-color: #fff;
  margin: 6px 0;
}

.header__navbar .navside {
  position: absolute;
  top: 0;
  right: 0;
  max-width: 100%;
  width: 350px;
  height: 100vh;
  background-color: rgb(20, 20, 20);
  padding: 75px 15px;
  transform: translateX(105%);
  transition: 0.3s ease;
}

.header__navbar .navside.nav-active {
  transform: translateX(0);
}

.header__navbar .navside .close__btn {
  position: absolute;
  top: 20px;
  right: 25px;
  cursor: pointer;
  width: 40px;
  height: 40px;
}

.header__navbar .navside .close__btn .bar {
  width: 28px;
  height: 3px;
  background-color: #fff;
}

.header__navbar .navside .close__btn .bar1 {
  transform: rotate(-45deg) translate(-7px, 16px);
}

.header__navbar .navside .close__btn .bar2 {
  transform: rotate(45deg) translate(14px, 5px);
}

.header__navbar .navside ul {
  z-index: 50;
}

.header__navbar .navside ul li a {
  position: relative;
  display: block;
  color: rgb(255, 255, 255);
  padding: 6px 0;
  font-size: 16px;
  transition: 0.2s ease;
}

.header__navbar .navside ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 2px;
  background-color: rgb(141, 141, 141);
  transition: 0.2s ease;
}

.header__navbar .navside ul li a:hover {
  padding-left: 7px;
}

.header__navbar .navside ul li a:hover::after {
  width: 100%;
}

.header__navbar .navside .social__icons {
  margin-top: 20px;
}

.header__navbar .navside .social__icons a {
  color: rgb(255, 255, 255);
  font-size: 23px;
  transition: 0.2s ease;
}

.header__navbar .navside .social__icons a:hover {
  color: rgb(255, 0, 0);
}

.header__navbar .navside .nav_name {
  position: absolute;
  top: 87%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  opacity: 0.6;
  letter-spacing: 1px;
}

.header__content {
  padding-top: 250px;
}

.content__blocks {
  display: flex;
  justify-content: space-between;
}

.content__blocks .content__text_block {
  max-width: 588px;
}

.content__blocks .content__text_block .content__title {
  font-size: 55px;
  font-weight: 700;
}

.content__blocks .content__text_block p {
  font-size: 24px;
  line-height: 34px;
  margin-top: 16px;
}

.content__blocks .content__text_block .watching {
  margin-top: 40px;
  max-width: 185px;
}

.content__blocks .content__text_block .watching a {
  display: flex;
  align-items: center;
  color: rgb(255, 255, 255);
}

.content__blocks .content__text_block .watching a span {
  font-size: 18px;
}

.content__blocks .content__text_block .watching a span:hover {
  border-bottom: 1px solid rgb(255, 255, 255);
}

.content__blocks .content__text_block .watching a .watch_play {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 126, 176);
  width: 35px;
  height: 35px;
  font-size: 15px;
  border-radius: 50%;
  margin-right: 10px;
}

.content__blocks .content__image_block {
  max-width: 650px;
  min-width: 400px;
}

.content__blocks .content__image_block img {
  width: 100%;
}

.toUpBtn {
  position: fixed;
  top: 120%;
  right: 2%;
  transition: 0.2s ease;
  z-index: 999;
}

.toUpBtn.toUpBtn-active {
  position: fixed;
  top: 85%;
}

.toUpBtn a {
  font-size: 40px;
  padding: 0 13px;
  background-color: rgb(0, 126, 176);
  color: rgb(255, 255, 255);
  border-radius: 5px;
  transition: 0.2s ease;
}

.toUpBtn a:hover {
  background-color: rgb(0, 86, 121);
}

.skills__section {
  padding-top: 110px;
}

.skills__blocks {
  display: flex;
  justify-content: space-between;
  column-gap: 20px;
  margin-top: 76px;
}

.skills__blocks .skills_block {
  background-color: rgb(32, 32, 32);
  max-width: 460px;
  padding: 40px 40px;
  text-align: center;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.16);
  border-radius: 20px;
  cursor: pointer;
  transition: 0.3s ease;
}

.skills__blocks .skills_block:hover {
  box-shadow: 0px 10px 16px rgba(0, 126, 176, 0.445);
}

.skills__blocks .skills_block .skilss_image {
  max-width: 140px;
  margin: 0 auto;
}

.skills__blocks .skills_block .skilss_image img {
  width: 100%;
}

.skills__blocks .skills_block .block_title {
  font-size: 24px;
  font-weight: 700;
  margin-top: 40px;
}

.skills__blocks .skills_block .block_text {
  font-weight: 300;
  font-size: 16px;
  letter-spacing: 1px;
  line-height: 22px;
  margin-top: 16px;
}

.videos__section {
  padding-top: 110px;
}

.videos__blocks {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin-top: 76px;
}

.videos__blocks .videos_block {
  position: relative;
  max-width: 460px;
  overflow: hidden;
  border-radius: 20px;
  cursor: pointer;
}

.videos__blocks .videos_block a img {
  width: 100%;
  height: 100%;
  transition: 0.2s ease;
}

.videos__blocks .videos_block a .play_video {
  position: absolute;
  top: 130%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 25px;
  transition: 0.3s ease;
  color: rgb(255, 255, 255);
}

.videos__blocks .videos_block:hover img {
  transform: scale(1.1);
  filter: brightness(60%);
}

.videos__blocks .videos_block:hover .play_video {
  position: absolute;
  top: 50%;
}

.about__section {
  padding-top: 110px;
}

.about__items {
  display: flex;
  justify-content: space-between;
  column-gap: 10px;
}

.about__items .about__item_text {
  max-width: 580px;
}

.about__items .about__item_text .about_title {
  text-align: start;
}

.about__items .about__item_text p {
  margin-top: 16px;
  font-size: 20px;
  line-height: 35px;
  letter-spacing: 0.8px;
}

.about__items .about__item_text p:nth-child(1) {
  margin-top: 32px;
}

.about__items .about__item_image {
  margin-top: 110px;
  max-width: 700px;
  min-width: 500px;
}

.about__items .about__item_image img {
  width: 100%;
}

.contact__section {
  padding-top: 110px;
}

.contact__items {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.contact__items .contact__image_item {
  max-width: 650px;
  min-width: 400px;
}

.contact__items .contact__image_item img {
  width: 100%;
}

.contact__items .contact__inputs_item {
  max-width: 700px;
  width: 100%;
}

.contact__items .contact__inputs_item input {
  width: 100%;
  height: 40px;
  margin: 10px 0;
  padding: 0 10px;
  font-size: 16px;
  border: none;
  border-radius: 10px;
  background-color: rgb(32, 32, 32);
  color: rgb(255, 255, 255);
  transition: 0.2s ease;
}

.contact__items .contact__inputs_item input:focus {
  border: 1px solid rgb(0, 126, 176);
  padding-left: 13px;
}

.contact__items .contact__inputs_item textarea {
  margin-top: 5px;
  width: 100%;
  min-height: 280px;
  border: none;
  border-radius: 10px;
  padding: 18px 10px;
  font-size: 16px;
  background-color: rgb(32, 32, 32);
  color: rgb(255, 255, 255);
  resize: none;
  transition: 0.2s ease;
}

.contact__items .contact__inputs_item textarea:focus {
  border: 1px solid rgb(0, 126, 176);
}

.contact__items .contact__inputs_item .contact_btn {
  padding: 10px 30px;
  margin-top: 10px;
  font-size: 17px;
  background-color: rgb(0, 126, 176);
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s ease;
}

.contact__items .contact__inputs_item .contact_btn:hover {
  background-color: rgb(0, 52, 73);
}

.footer__section {
  margin-top: 110px;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  padding-top: 32px;
}

.footer__items {
  display: flex;
  justify-content: space-between;
}

.footer__logo_item {
  max-width: 453px;
}

.footer__logo_item .footer__logo_svg {
  max-width: 272px;
}

.footer__logo_item .footer__logo_svg .footer__logo_icon {
  fill: rgb(255, 255, 255);
  width: 100%;
}

.footer__logo_item .footer__text {
  margin-top: 24px;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
}

.footer_item {
  max-width: 453px;
}

.footer_item .social__title {
  font-size: 24px;
  letter-spacing: 1px;
  color: rgba(255, 255, 255, 0.5);
}

.footer_item .social__links ul {
  margin-top: 32px;
}

.footer_item .social__links ul li a {
  display: block;
  padding: 12px 0;
  font-size: 20px;
  letter-spacing: 1px;
  color: rgb(255, 255, 255);
  transition: 0.2s ease;
}

.footer_item .social__links ul li a:hover {
  color: rgb(0, 126, 176);
}

.copyright {
  padding: 24px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  text-align: center;
  max-width: 700px;
  margin: 0 auto;
  margin-top: 50px;
}

.copyright p {
  font-size: 16px;
  letter-spacing: 1px;
}

.copyright p:nth-child(2) {
  margin-top: 12px;
  font-weight: 600;
}

.copyright p:nth-child(2) a {
  color: rgb(255, 255, 255);
  font-weight: 400;
  transition: 0.2s ease;
}

.copyright p:nth-child(2) a:hover {
  color: rgb(0, 126, 176);
}

/*====================== media queries for responsive ==========================*/

@media screen and (min-width: 941px) {
  .header__navbar .navside {
    position: absolute;
    top: 0;
    right: -105%;
  }
}

@media screen and (max-width: 1180px) {
  .skills__blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 20px;
  }

  .skills__blocks .skills_block {
    max-width: 100%;
  }
}

@media screen and (max-width: 1040px) {
  .about__items .about__item_text p {
    font-size: 18px;
    line-height: 27px;
  }

  .footer__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
}

@media screen and (max-width: 940px) {
  .header__navbar .nav-links {
    display: none;
  }

  .header__navbar .color_theme_icon {
    margin: 0 35px;
  }

  .header__navbar .burger {
    display: block;
  }

  .content__blocks {
    flex-direction: column;
    align-items: center;
  }

  .content__blocks .content__text_block .watching {
    max-width: 220px;
  }

  .content__blocks .content__image_block {
    max-width: 100%;
    min-width: 100%;
  }

  .videos__blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .videos__blocks .videos_block {
    max-width: 100%;
  }

  .about__items {
    flex-direction: column;
  }

  .about__items .about__item_image {
    max-width: 100%;
    min-width: 100%;
  }

  .contact__items {
    flex-direction: column;
  }

  .contact__items .contact__image_item {
    max-width: 100%;
    min-width: 100%;
  }
}

@media screen and (max-width: 670px) {
  .skills__blocks {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .videos__blocks {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }

  .footer__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 530px) {
  .header__content {
    margin-top: 150px;
  }

  .content__blocks .content__text_block .content__title {
    font-size: 40px;
  }

  .content__blocks .content__text_block p {
    font-size: 20px;
    line-height: 30px;
  }
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 25px;
  }

  .content__blocks .content__text_block .content__title {
    font-size: 33px;
  }

  .content__blocks .content__text_block p {
    font-size: 18px;
    line-height: 27px;
  }
}